import { zip, map, indexed, choose } from "./.fable/fable-library.3.2.11/Array.js";
import { join, split } from "./.fable/fable-library.3.2.11/String.js";

function findAllIndexes(predicate, array) {
    return choose((tupledArg) => {
        if (predicate(tupledArg[1])) {
            return tupledArg[0];
        }
        else {
            return void 0;
        }
    }, indexed(array), Int32Array);
}

export function getExamples(examplesHtml) {
    const lines = split(examplesHtml, ["\n", "\r"]);
    return map((tupledArg) => {
        const s = tupledArg[0] | 0;
        return [split(lines[s], [" "], null, 0)[2], join("\n", lines.slice(s + 1, (tupledArg[1] - 1) + 1))];
    }, zip(findAllIndexes((line) => (line.indexOf("{% capture") === 0), lines), findAllIndexes((line_1) => (line_1 === "{% endcapture %}"), lines)));
}

